<template>
  <div class="classDetailWrap">
    <div class="breadcrumbs">
      <span @click="skipIndex"></span>>
      <span @click="skipList"></span>>
      <span></span>
    </div>
    <div class="classDetail">
      <div class="classVideo">
        <div class="classInfo noclass" v-if="!selClass"></div>
        <div class="classInfo" v-if="selClass&&selClass.courseType=='1'&&selClass.courseUrl">
          <iframe width="100%" height="100%" v-bind:src="selClass.courseUrl"></iframe>
        </div>
        <div class="classInfo" v-if="selClass&&selClass.courseType=='2'&&selClass.courseUrl">
          <iframe width="100%" height="100%" v-bind:src="selClass.courseUrl"></iframe>
        </div>
        <div class="classInfo" v-if="selClass&&selClass.courseType=='0'&&selClass.courseUrl">
          <video
            id="viedoDom"
            width="100%"
            v-bind:src="selClass.courseUrl"
            controls="controls"
          > video </video>
        </div>
      </div>
      <div class="videoBottom">
        <div class="sign" v-on:click="showModal">
          <a-icon type="star"/>
          <span></span>
        </div>
        <div class="sign">
          <a-icon type="download"/>
          <span></span>
        </div>
        <div class="sign">
          <a-icon type="heart"/>
          <span></span>
        </div>
      </div>
      <div class="centerCont">
        <div class="leftCatalog">
          <div class="detailTitle" v-text="detailObj.packageName"></div>
          <ul class="listUl">
            <li v-for="item in detailObj.contents" v-on:click="classClick(item)">
              <div>
                <a-icon
                  type="check-circle"
                  theme="twoTone"
                  twoToneColor="#00BD70"
                  v-if="item.finished==='1'"
                />
                <a-icon type="reload" style="color:#F0AE43;" v-if="item.finished!=='1'"/>
                <span v-text="item.courseName">1</span>
              </div>
              <div>
                <span class="time" v-text="item.timeLentgh">05:30</span>
                <a-icon type="play-circle" theme="outlined" outlinedColor="#7F7F7F"/>
              </div>
            </li>
          </ul>
          <div class="ceshiButton">
             <a-button type="primary" v-on:click="testShow()"></a-button>
          </div>
        </div>
        <div class="rightTeacher">
          <div class="detailTitle"></div>
          <div class="person">
            <img width="100%" src="../../static/images/teacher.png">
            <div class="name" v-text="detailObj.teacherName"></div>
          </div>
          <div class="info"></div>
          <div class="info">15689898989</div>
          <div class="info">yong@persagy.com</div>
        </div>
      </div>
      <div class="cIntroduce">
        <div class="detailTitle"></div>
        <div class="text">
          
          
        </div>
      </div>
      <div class="commentBox">
        <div class="person">
          <img width="100%" src="../../static/images/user2.jpg">
          <div class="name"></div>
        </div>
        <div class="comment">
          <textarea></textarea>
        </div>
        <div class="commentButton">
          <a-button type="primary"></a-button>
        </div>
      </div>
      <div class="allComment">
        <div class="allTitle">103</div>
        <div class="eachComment">
          <div class="leftImage">
            <img width="100%" src="../../static/images/user2.png">
          </div>
          <div class="rightCont">
            <div class="title">
              <span></span>
              <span>2019/09/01 12:25</span>
            </div>
            <div class="cont"></div>
            <div class="mark">
              <a-icon type="like"/>
              <span>3</span>
              <a-icon type="message"/>
              <span></span>
            </div>
          </div>
        </div>
      </div>
      <div class="dialogBox">
        <a-modal title="" v-model="dvisible" @ok="handleOk" okText="">
          <p class="diaTitle"></p>
          <div class="diaStar">
            <a-rate v-model="starvalue"/>
          </div>
          <div class="comment dialogComment">
            <textarea></textarea>
          </div>
        </a-modal>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "classDetail",
  data() {
    return {
      dvisible: false,
      starvalue: 2,
      detailObj: {},
      selClass: null,
      commentList: [
        {
          userId: "mock", //String    
          comment: "mock", //String    
          createTime: "mock" //String    
        }
      ]
    };
  },
  methods: {
    testShow: function() {
      this.$router.push({ path: "/test" });
    },
    showModal() {
      this.dvisible = true;
    },
    handleOk(e) {
      console.log(e);
      this.dvisible = false;
    },
    skipIndex: function() {
      //this.$router.push({ name: "classMain" });
      this.$router.push({ path: "/" });
    },
    skipList: function() {
      this.$router.push({ name: "classList" });
    },
    getDetail: function(params) {
      var that = this;
      this.$axios
        .post(
          "http://192.168.100.233:8888/geek-knife/course/getCourseDetails",
          {
            userId: params.userId,
            packageId: params.packageId
          }
        )
        .then(function(response) {
          that.detailObj = response.data;
        })
        .catch(function(error) {
          var ss = 1;
        });
    },
    getComment: function(params) {
      var that = this;
      this.$axios
        .post(
          "http://192.168.100.236:31002/geek-knife/course/listCourseComment",
          {
            packageId: params.packageId
          }
        )
        .then(function(response) {
          that.detailObj = response.data || {};
        })
        .catch(function(error) {
          var ss = 1;
        });
    },
    classClick: function(item) {
      this.selClass = item;
      //document.getElementById("viedoDom").setAttribute("src", item.courseUrl);
      console.log("xxx");
    }
  },
  computed: {},
  created() {
    var params = this.$route.params;
    this.getDetail(params);
    this.getComment(params);
  },
  mounted() {},
  components: {},
  watch: {}
};
</script>
